				content: "";
				position: absolute;
				left: 0;
				top: 10px;
				width: 100%;
				height: 3px;
				background: $main-color;
				transform-origin: 50% 50%;
				transition: top .3s .3s,transform .3s; }
			&:after {
				content: "";
				position: absolute;
				left: 0;
				top: -10px;
				width: 100%;
